import { Component, OnInit, Inject, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { Observable } from "rxjs";
import { RegexService } from '../../../public-share/service/regex.service';
import { ApplicationModalConfig } from '../../../models/modal';
import { MODAL_DATA, PARENT_FORM, CHILD_FORM } from '../../../public-share/component/modal/modal.token';
import { BaseModal } from "../../../public-share/component/modal/modal.base";
import { AccountMgmtApiUrlService } from "../service/account-management-api-url.service";
import { UserPostSchema, UserPutSchema, UserRoleType } from '../../../models/account-management';
import { APP_MARSMIDDLE_USER_ROLE } from '../../login/service/login.service';

@Component({
  selector: 'app-site-form-modal',
  templateUrl: './account-management-modal.component.html',
  styleUrls: ['../account-management.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AccountMgmtFormModalComponent implements OnInit, BaseModal {

  roleOptions: UserRoleType[] = [UserRoleType.administrator, UserRoleType.guest];
  currentUserRole: string = localStorage.getItem(APP_MARSMIDDLE_USER_ROLE);

  accountMgmtForm = this.formBuilder.group({
    userName: ['', [Validators.required, Validators.pattern(this.regexService.getRule('username_5_20'))]],
    password: ['', [Validators.required, Validators.pattern(this.regexService.getRule('password_8'))]],
    role: [this.roleOptions[1], [Validators.required]]
  });

  updateFlag: boolean = false;
  updateInfo: UserPutSchema;
  updateUserName: string;

  constructor(
    @Inject(MODAL_DATA) public modalConfig: ApplicationModalConfig,
    private formBuilder: FormBuilder,
    private accountMgmtApiUrlService: AccountMgmtApiUrlService,
    private regexService: RegexService
  ) { }

  ngOnInit(): void {
    if (this.modalConfig.data && this.modalConfig.data.updateFlag) {
      this.updateFlag = true;
      this.updateInfo = this.modalConfig.data.updateInfo;
      this.updateUserName = this.modalConfig.data.username;
      this.accountMgmtForm.get('userName').setValue(this.updateUserName);
      this.accountMgmtForm.get('userName').disable();
      this.accountMgmtForm.get('password').setValidators([Validators.pattern(this.regexService.getRule('password_8'))]);
      this.accountMgmtForm.get('role').setValue(this.updateInfo.role);
      this.accountMgmtForm.get('role').setValidators([]);
      if (this.currentUserRole != UserRoleType.administrator) {
        this.accountMgmtForm.get('role').disable();
      }
    }
    this.modalConfig[PARENT_FORM].addControl(CHILD_FORM, this.accountMgmtForm);
  }

  submit(): Observable<any> {
    const data = this.accountMgmtForm.getRawValue();

    return new Observable<any>(subscriber => {
      if (this.updateFlag == true) {
        const putUserParams: UserPutSchema = {};
        if (data.password) { putUserParams.password = data.password; }
        if (data.role && this.currentUserRole == UserRoleType.administrator) { putUserParams.role = data.role; }

        this.accountMgmtApiUrlService.putUser(this.updateUserName, putUserParams)
        .subscribe(
          data =>
            subscriber.next(true),
          err =>
            subscriber.error(err)
        );
      } else {
        const signUpUserParams: UserPostSchema = {
          username: data.userName,
          password: data.password,
          role: data.role
        };
        this.accountMgmtApiUrlService.signUpUser(signUpUserParams)
        .subscribe(
          data =>
            subscriber.next(true),
          err =>
            subscriber.error(err)
        );
      }
    });
  }

}
