import { Component, OnInit, ViewChild, TemplateRef, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RegexService } from '../../public-share/service/regex.service';
import { LoginService } from '../login/service/login.service';
import { ToastrOperatorService } from '../../public-share/service/toastr-operator.service';
import { Subject, BehaviorSubject, Observable } from 'rxjs';
import { AccountMgmtService } from './service/account-management.service';
import { AccountMgmtTableModelService } from './service/account-management-table-model.service';
import { DataTableModel } from '../../models/data-table';

@Component({
  selector: 'app-account-management',
  templateUrl: './account-management.component.html',
  styleUrls: ['./account-management.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AccountManagementComponent implements OnInit {
  @ViewChild("accountMgmtMultiActions", { static: true }) accountMgmtMultiActionsRef: TemplateRef<any>;

  accountMgmtTableModel: DataTableModel;
  currentUser: string;
  isGuest: boolean;

  readonly accountMgmtTableData$: Observable<any[]> = this.accountMgmtService.accountMgmtTableData$;
  readonly loading$: Observable<boolean> = this.accountMgmtService.loading$;

  constructor(
    private formBuilder: FormBuilder,
    private regexService: RegexService,
    private toastrOperatorService: ToastrOperatorService,
    private accountMgmtService: AccountMgmtService,
    private accountMgmtTableModelService: AccountMgmtTableModelService,
    private loginService: LoginService

  ) { }

  ngOnInit(): void {
    this.initialTableModel();
    this.refreshTableData();
    this.currentUser = this.loginService.getCurrentUsername();
    this.isGuest = this.loginService.isGuestUser();
  }

  refreshTableData(): void {
    this.accountMgmtService.getAccountMgmtTableData();
  }

  initialTableModel(): void {
    this.accountMgmtTableModel = this.accountMgmtTableModelService.getAccountMgmtTableModel();
    this.updateTableCellTemplate(this.accountMgmtTableModel);
  }

  updateTableCellTemplate(tableModel: DataTableModel): void {
    tableModel.columns.forEach((item) => {
      if (item.id === 'multiActions') {
          item.customCellTemplate = this.accountMgmtMultiActionsRef;
      }
    });
  }

  addUser(): void {
    this.accountMgmtService.addUser();
  }

  delete(rowData, $event): void {
    this.accountMgmtService.deleteUserAccount(rowData.username);
  }

  update(rowData, $event): void {
    this.accountMgmtService.updateUserAccount(rowData.username, rowData);
  }

}
