import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable, of, Subject } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { CookieOptions, CookieService } from "ngx-cookie";

import { apiUrl } from '../../../public-share/service/environment';
import { User } from '../../../models/login';
import { UserRoleType } from '../../../models/account-management';
import { ToastrOperatorService } from '../../../public-share/service/toastr-operator.service';

export const APP_MARSMIDDLE_LOGIN_USER: string = 'app_login_user';
export const APP_MARSMIDDLE_USER_ROLE: string = 'app_user_role';
export const APP_MARSMIDDLE_LOGIN_TOKEN: string = 'app_login_token';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  // public routeState = {};
  // private isFirst = true;
  public isLoggedIn: boolean = false;

  constructor(private router: Router,
              // private cookieService: CookieService,
              private toastrOperatorService: ToastrOperatorService,
              private http: HttpClient) {
  }

  public postUserLogin(user: User) {
    return this.http.post(`${apiUrl}/users/login`, user, {observe: "response"});
  }

  public postUserLogout() {
    return this.http.post(`${apiUrl}/users/logout`, {}, {observe: "response"});
  }

  loginLocal(user: User) {
    return this.postUserLogin(user)
            .pipe(
              take(1),
              map((res: HttpResponse<any>) => {
                const username = res.body.username;
                const role = res.body.role;
                const token = res.body.token;

                localStorage.setItem(APP_MARSMIDDLE_LOGIN_USER, username);
                localStorage.setItem(APP_MARSMIDDLE_USER_ROLE, role);
                localStorage.setItem(APP_MARSMIDDLE_LOGIN_TOKEN, token);
                this.isLoggedIn = true;
                return res;
              })
            );
  }

  logout() {
    // const loginServer = this.cookieService.get('loginServer');
    let logoutObservable;

    // if (loginServer === 'local') {
      logoutObservable = this.postUserLogout();
    // } else if (loginServer === 'radius') {
    //   logoutObservable = this.logoutRadius();
    // }

    logoutObservable.subscribe(res => {
      this.redirectToLogin();
    }, (error) => {
      console.log("login error ..." + JSON.stringify(error));
      this.toastrOperatorService.showErrorToastr("login error ..." + JSON.stringify(error));
    });
  }

  redirectToLogin() {
    localStorage.removeItem(APP_MARSMIDDLE_LOGIN_USER);
    localStorage.setItem(APP_MARSMIDDLE_LOGIN_TOKEN, '');
    this.router.navigate(['/login']);
  }

  public isGuestUser(): boolean {
    return localStorage.getItem(APP_MARSMIDDLE_USER_ROLE) == UserRoleType.guest;
  }

  public getCurrentUsername(): string {
    return localStorage.getItem(APP_MARSMIDDLE_LOGIN_USER);
  }
  
  // getHeadersWithToken(): HttpHeaders {
  //   const loginToken: string = localStorage.getItem(APP_MARSMIDDLE_LOGIN_TOKEN) || '';
  //   return new HttpHeaders({
  //     'Authorization': 'Bearer '+ loginToken, 
  //     'Content-Type': 'application/x-www-form-urlencoded'
  //   });
  // }

  // public pageLoader(): Observable<boolean> {
  //   if (!this.isFirst) {
  //     return of(true);
  //   }
  //   return this.applicationLoader();
  // }
}
