import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Column, DataTableModel } from '../../../models/data-table';

@Injectable({
  providedIn: 'root'
})
export class AccountMgmtTableModelService {
  constructor(
    private translate: TranslateService
  ) { }

  getAccountMgmtTableModel(): DataTableModel {
    let columns: Column[] = [
      {
        id: 'username',
        displayName: this.translate.instant('PAGE.ACCOUNT_MGMT.COLUMN.USERNAME'),
        maxWidth: '360px'
      },
      {
        id: 'role',
        displayName: this.translate.instant('PAGE.ACCOUNT_MGMT.COLUMN.ROLE')
      },
      {
        id: 'multiActions',
        displayName: this.translate.instant('PAGE.SITE.COLUMN.ACTIONS'),
        sort: 'disabled',
        maxWidth: '160px'
      }
    ];

    return {
      columns: columns,
      hiddenRefresh: true,
      hiddenCustomize: true
    };
  }

}