import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { apiUrl } from '../../../public-share/service/environment';
import {
  UserRes,
  UserPostSchema,
  UserPutSchema
} from '../../../models/account-management';

@Injectable({
  providedIn: 'root'
})
export class AccountMgmtApiUrlService {

  constructor(
    private http: HttpClient
  ){ }

  public getUsers(): Observable<UserRes[]> {
    return this.http.get<UserRes[]>(`${apiUrl}/users`);
  }

  public signUpUser(userInfo: UserPostSchema) {
    return this.http.post(`${apiUrl}/users/signup`, userInfo);
  }

  public putUser(userName: string, userInfo: UserPutSchema) {
    return this.http.put(`${apiUrl}/users/${userName}`, userInfo);
  }

  public deleteUser(userName: string) {
    return this.http.delete(`${apiUrl}/users/${userName}`);
  }

}