import { Component, OnInit, Inject, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { Observable } from "rxjs";
import { RegexService } from '../../../public-share/service/regex.service';
import { ApplicationModalConfig } from '../../../models/modal';
import { MODAL_DATA, PARENT_FORM, CHILD_FORM } from '../../../public-share/component/modal/modal.token';
import { BaseModal } from "../../../public-share/component/modal/modal.base";
import { SiteApiUrlService } from "../service/siteApiUrl.service";
import { SitePostSchema, SitePutSchema } from '../../../models/site';

@Component({
  selector: 'app-site-form-modal',
  templateUrl: './siteFormModal.component.html',
  styleUrls: ['../sites.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SiteFormModalComponent implements OnInit, BaseModal {

  siteForm = this.formBuilder.group({
    siteName: ['', [Validators.required, Validators.pattern(this.regexService.getRule('name_en_15'))]],
    siteDescription: ['', []]
  });

  updateFlag: boolean = false;
  updateInfo: SitePutSchema;
  updateSiteName: string;

  constructor(
    @Inject(MODAL_DATA) public modalConfig: ApplicationModalConfig,
    private formBuilder: FormBuilder,
    private siteApiUrlService: SiteApiUrlService,
    private regexService: RegexService
  ) { }

  ngOnInit(): void {
    if (this.modalConfig.data && this.modalConfig.data.updateFlag) {
      this.updateFlag = true;
      this.updateInfo = this.modalConfig.data.updateInfo;
      this.updateSiteName = this.updateInfo.siteName;
      this.siteForm.get('siteName').setValue(this.updateInfo.siteName);
      this.siteForm.get('siteName').setValidators([Validators.pattern(this.regexService.getRule('name_en_15'))]),
      this.siteForm.get('siteDescription').setValue(this.updateInfo.siteDescription);
    }
    this.modalConfig[PARENT_FORM].addControl(CHILD_FORM, this.siteForm);
  }

  submit(): Observable<any> {
    const data = this.siteForm.getRawValue();

    return new Observable<any>(subscriber => {
      if (this.updateFlag == true) {
        const putSiteParams: SitePutSchema = {
          siteName: data.siteName,
          siteDescription: data.siteDescription
        };
        if (data.siteName == '' || data.siteName === this.updateSiteName) {
          delete putSiteParams.siteName;
        }
        this.siteApiUrlService.putSite(this.updateSiteName, putSiteParams)
        .subscribe(
          data =>
            subscriber.next(true),
          err =>
            subscriber.error(err)
        );
      } else {
        const postSiteParams: SitePostSchema = {
          siteName: data.siteName,
          siteDescription: data.siteDescription
        };
        this.siteApiUrlService.postSite(postSiteParams)
        .subscribe(
          data =>
            subscriber.next(true),
          err =>
            subscriber.error(err)
        );
      }
    });
  }

}
