import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { TranslateService } from '@ngx-translate/core';
import { apiUrl } from '../../../public-share/service/environment';
import { MockDataService } from '../../../public-share/service/mock-data.service';
import { ToastrOperatorService } from '../../../public-share/service/toastr-operator.service';
import { catchError } from 'rxjs/operators';
import { of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PageHandlerService {

  constructor(
    private mockDataService: MockDataService,
    private http: HttpClient,
    private toastrOperatorService: ToastrOperatorService,
    private translate: TranslateService
  ) { }

  getEventData() {
    return this.mockDataService.getMockEvents();
  }

  async getErrorMassageMultiLanguage(): Promise<void> {
    this.http.get(`${apiUrl}/errorMessage`)
    .pipe(
      catchError((error) => {
        const errorMessage = error.error?.error?.message || error.message || error;
        this.toastrOperatorService.showErrorToastr(errorMessage, this.translate.instant('COMMON.MESSAGE.TITLE.GET_MLTI_LANG'));
        return of({});
      })
    )
    .subscribe(response => {
      if (Object.keys(response).length != 0) {
        localStorage['marsMiddleErrorMassageMultiLanguage'] = JSON.stringify(response);
      }
    })
  }

}
