import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router';
import {LoginService, APP_MARSMIDDLE_USER_ROLE, APP_MARSMIDDLE_LOGIN_TOKEN} from '../../pages/login/service/login.service'

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {
  constructor(
    private loginService: LoginService,
    private router: Router
  ) { }

  canActivate(): boolean {
    return this.checkLogin();
  }

  checkLogin(): boolean {
    const loginToken: string = localStorage.getItem(APP_MARSMIDDLE_LOGIN_TOKEN);
    // console.log('AuthGuard#canActivate called', loginToken);
    if (!loginToken) {
      this.router.navigate(['/login'], {queryParams: {returnUrl: window.location.pathname}});
      return false;
    }
    return true; 
  }

}
