import { Component, OnInit, ViewChild, TemplateRef, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RegexService } from '../../public-share/service/regex.service';
import { LoginService } from '../login/service/login.service';
import { ToastrOperatorService } from '../../public-share/service/toastr-operator.service';
import { Subject, BehaviorSubject, Observable } from 'rxjs';
import { SiteService } from './service/site.service';
import { SiteTableModelService } from './service/siteTableModel.service';
import { DataTableModel } from '../../models/data-table';

@Component({
  selector: 'app-sites',
  templateUrl: './sites.component.html',
  styleUrls: ['./sites.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SitesComponent implements OnInit {
  @ViewChild('controllers', { static: true }) controllersRef: TemplateRef<any>;
  @ViewChild("siteMultiActions", { static: true }) siteMultiActionsRef: TemplateRef<any>;
  @ViewChild("controllerMultiActions", { static: true }) controllerMultiActionsRef: TemplateRef<any>;
  @ViewChild("controllerLink", { static: true }) controllerLinkRef: TemplateRef<any>;
  @ViewChild("controllerLoginStatus", { static: true }) controllerLoginStatusRef: TemplateRef<any>;
  @ViewChild("controllerCpuUsagePercentage", { static: true }) controllerCpuUsagePercentageRef: TemplateRef<any>;
  @ViewChild("controllerRamUsagePercentage", { static: true }) controllerRamUsagePercentageRef: TemplateRef<any>;
  @ViewChild("controllerAvailableDevices", { static: true }) controllerAvailableDevicesRef: TemplateRef<any>;
  @ViewChild("controllerErrorLog", { static: true }) controllerErrorLogRef: TemplateRef<any>;
  @ViewChild("controllerErrorLogTemplate", { static: true }) controllerErrorLogTemplateRef: TemplateRef<any>;

  siteTableModel: DataTableModel;
  controllerTableModel: DataTableModel;
  isGuest: boolean;

  readonly siteTableData$: Observable<any[]> = this.siteService.siteTableData$;
  readonly loading$: Observable<boolean> = this.siteService.loading$;

  constructor(
    private formBuilder: FormBuilder,
    private regexService: RegexService,
    private toastrOperatorService: ToastrOperatorService,
    private siteService: SiteService,
    private siteTableModelService: SiteTableModelService,
    private loginService: LoginService
  ) {
    this.isGuest = this.loginService.isGuestUser();
  }

  ngOnInit(): void {
    this.initialTableModel();
    this.refreshTableData();
  }

  refreshTableData(): void {
    this.siteService.getSitesTableData();
  }

  initialTableModel(): void {
    this.siteTableModel = this.siteTableModelService.getSiteTableModel();
    this.updateTableCellTemplate(this.siteTableModel, 'site');

    this.controllerTableModel = this.siteTableModelService.getControllerTableModel();
    this.updateTableCellTemplate(this.controllerTableModel, 'controller');

    this.siteTableModel.expandRowTemplate = this.controllersRef;
  }

  updateTableCellTemplate(tableModel: DataTableModel, level?: string): void {
    tableModel.columns.forEach((item) => {
      if (item.id === 'multiActions') {
        if (level === 'site') {
          item.customCellTemplate = this.siteMultiActionsRef;
        } else if (level === 'controller') {
          item.customCellTemplate = this.controllerMultiActionsRef;
        }
      }  else if (item.id === 'controllerName') {
        item.customCellTemplate = this.controllerLinkRef;
      }  else if (item.id === 'loginStatus') {
        item.customCellTemplate = this.controllerLoginStatusRef;
      } else if (item.id === 'cpuUsagePercentage') {
        item.customCellTemplate = this.controllerCpuUsagePercentageRef;
      } else if (item.id === 'ramUsagePercentage') {
        item.customCellTemplate = this.controllerRamUsagePercentageRef;
      } else if (item.id === 'availableDevices') {
        item.customCellTemplate = this.controllerAvailableDevicesRef;
      } else if (item.id === 'errorLog') {
        item.customCellTemplate = this.controllerErrorLogRef;
      }
    });
  }

  addSite(): void {
    this.siteService.addSite();
  }

  addController(rowData, $event): void {
    $event.stopPropagation();
    this.siteService.addController(rowData.siteName);
  }

  delete(rowData, $event, level: ('site'|'controller')): void {
    switch (level) {
      case 'site':
          $event.stopPropagation();
          this.siteService.deleteSite(rowData.siteName);
          break;
      case 'controller':
          this.siteService.deleteController(rowData.siteName, rowData.controllerName);
          break;
    }
  }

  update(rowData, $event, level: ('site'|'controller')): void {
    switch (level) {
      case 'site':
          $event.stopPropagation();
          this.siteService.updateSite(rowData);
          break;
      case 'controller':
          this.siteService.updateController(rowData.siteName, rowData);
          break;
    }
  }

  getControllerErrorLog(siteName: string, controllerName: string, errorLog: string[], $event): void {
    $event.stopPropagation();
    this.siteService.getControllerErrorLog(siteName, controllerName, errorLog, this.controllerErrorLogTemplateRef);
  }

  navigate2ControllerWeb(rowData): void {
    const controllerIp: string = rowData.ipAddress;
    window.open(`https://${controllerIp}`, '_blank');
  }

  getPercentageProgressStyleParams(percentage: number): string {
    let color: string;
    if(percentage>80) {
      color = '#f1d1c8'; // RED
    } else {
      color = '#9ed68a'; // GREEN
    }
    return `--progress-color: ${color}; --progress: ${percentage}%;`;
  }

}
