import { Injectable, OnDestroy, OnInit, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Subject, BehaviorSubject, Observable, of, forkJoin } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import {
  SiteTableData,
  SitePostSchema,
  SitePutSchema,
  SiteRes,
  ControllerTableData,
  ControllerPostSchema,
  ControllerPutSchema,
  SiteErrorLogRes,
  ControllerErrorLogRes
} from '../../../models/site';
import { BasicModal, ApplicationModalConfig } from '../../../models/modal';
import { SiteApiUrlService } from './siteApiUrl.service';
import { ToastrOperatorService } from '../../../public-share/service/toastr-operator.service';
import { BasicModalService, ApplicationModalService } from '../../../public-share/component/modal/modal.service';
import { SiteFormModalComponent } from '../formModal/siteFormModal.component';
import { ControllerFormModalComponent } from '../formModal/controllerFormModal.component';

@Injectable({
  providedIn: 'root'
})
export class SiteService implements OnInit, OnDestroy {

  constructor(
    private router: Router,
    private toastrOperatorService: ToastrOperatorService,
    private siteApiUrlService: SiteApiUrlService,
    private appModalService: ApplicationModalService,
    private basicModalService: BasicModalService,
    private translate: TranslateService
  ) { }

  private _siteTableData = new BehaviorSubject<SiteTableData[]>([]);
  private _loading = new BehaviorSubject<boolean>(true);
  // private _message = new Subject<MessageModel>();
  readonly siteTableData$: Observable<any> = this._siteTableData.asObservable();
  readonly loading$: Observable<boolean> = this._loading.asObservable();
  // readonly message$: Observable<MessageModel> = this._message.asObservable();

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this._siteTableData.unsubscribe();
    this._loading.unsubscribe();
    // this._message.unsubscribe();
  }

  getSitesTableData(): void {
    this._loading.next(true);
    
    const sitesData = this.siteApiUrlService.getSites()
      .pipe(
        catchError((error) => {
          const errorMessage = error.error?.error?.message || error.message || error;
          this.toastrOperatorService.showErrorToastr(errorMessage, this.translate.instant('PAGE.SITE.MESSAGE.TITLE.GET_SITE_TABLE_DATA'));
          return of([]);
        })
      )

    const hour: number = 8;
    const count: number = 100;
    const sitesErrorLogData = this.siteApiUrlService.getSitesErrorLog(hour, count)
      .pipe(
        catchError((error) => {
          const errorMessage = error.error?.error?.message || error.message || error;
          this.toastrOperatorService.showErrorToastr(errorMessage, this.translate.instant('PAGE.SITE.MESSAGE.TITLE.GET_SITES_ERROR_LOG'));
          return of([]);
        })
      )

    forkJoin([sitesData, sitesErrorLogData])
      .subscribe(([sitesRes, sitesErrorLogRes]) => {
        const siteTableData: SiteTableData[] = [];
        sitesRes.forEach((site) => {
          siteTableData.push({
            siteName: site.siteName,
            siteDescription: site.siteDescription,
            controllers: site.controllers,
            controllerCounts: site.controllers.length
          });
        })
        siteTableData.forEach((site) => {
          let _controllersErrLog = sitesErrorLogRes.find((_site) => {return _site.siteName === site.siteName})?.controllers || [];
          site.controllers.forEach( (controller) => {
            controller.siteName = site.siteName;
            controller.cpuUsagePercentage = (controller.cpuIdle >= 0)? (100-controller.cpuIdle) : -1;
            controller.ramUsagePercentage = (controller.ramUsage >= 0)? controller.ramUsage : -1;
            const availableDeviceCountsString: string = (controller.availableDeviceCounts >= 0)? controller.availableDeviceCounts.toString() : '-';
            const deviceCountsString: string = (controller.deviceCounts >= 0)? controller.deviceCounts.toString() : '-';
            controller.availableDevices = availableDeviceCountsString + ' / ' + deviceCountsString;

            let _controllerErrLog = _controllersErrLog.find((_controller) => {return _controller.controllerName === controller.controllerName});
            controller.errorLog = _controllerErrLog?.errorLog.reverse() || controller.errorLog;
          })
        });

        this._loading.next(false);
        this._siteTableData.next(JSON.parse(JSON.stringify(siteTableData)));
    });

  }

  addSite(): void {
    this.openSiteFormModal('add');
  }

  addController(siteName: string): void {
    this.openControllerFormModal('add', siteName);
  }

  updateSite(rowData: SiteTableData): void {
    const updateInfo: SitePutSchema = {
      siteName: rowData.siteName,
      siteDescription: rowData.siteDescription
    };
    this.openSiteFormModal('update', updateInfo);
  }

  updateController(siteName: string, rowData: ControllerTableData): void {
    const updateInfo: ControllerPutSchema = {
      controllerName: rowData.controllerName,
      ipAddress: rowData.ipAddress,
      loginAccount: rowData.loginAccount,
      description: rowData.description
    }
    this.openControllerFormModal('update', siteName, updateInfo);
  }

  openSiteFormModal(action: ('add'|'update'), updateInfo?: SitePutSchema): void {
    let modalConfig: ApplicationModalConfig;
    let toastrTitle: string = '';
    let successMessage: string = '';
    
    switch(action) {
      case 'add':
          modalConfig = {
                          title: this.translate.instant('PAGE.SITE.ACTION.ADD_SITE')
                        };
          toastrTitle = this.translate.instant('PAGE.SITE.MESSAGE.TITLE.ADD_SITE');
          successMessage = this.translate.instant('PAGE.SITE.MESSAGE.SUCCESS.ADD_SITE');
          break;
      case 'update':
          modalConfig = {
                          title: this.translate.instant('PAGE.SITE.ACTION.UPDATE_SITE'),
                          data: {
                                  updateInfo: updateInfo,
                                  updateFlag: true
                                }
                        };
          toastrTitle = this.translate.instant('PAGE.SITE.MESSAGE.TITLE.UPDATE_SITE');
          successMessage = this.translate.instant('PAGE.SITE.MESSAGE.SUCCESS.UPDATE_SITE');
          break;
    }

    const modalRef = this.appModalService.open(SiteFormModalComponent, modalConfig);

    modalRef.afterClose$.subscribe(
      res => {
        if (!res.data) return;
        this.toastrOperatorService.showSuccessToastr(successMessage, toastrTitle);
        this.getSitesTableData();
      },
      error => {
        const errorMessage = error.error?.error?.message || error.message || error;
        this.toastrOperatorService.showErrorToastr(errorMessage, toastrTitle);
      }
    )
  }

  openControllerFormModal(action: ('add'|'update'), siteName: string, updateInfo?: ControllerPutSchema): void {
    let modalConfig: ApplicationModalConfig;
    let toastrTitle: string = '';
    let successMessage: string ='';
    
    switch(action) {
      case 'add':
          modalConfig = {
                          title: this.translate.instant('PAGE.SITE.ACTION.ADD_CTRL'),
                          data: {
                                  siteName: siteName
                                }
                        };
          toastrTitle = this.translate.instant('PAGE.SITE.MESSAGE.TITLE.ADD_CTRL');
          successMessage = this.translate.instant('PAGE.SITE.MESSAGE.SUCCESS.ADD_CTRL');
          break;
      case 'update':
          modalConfig = {
                          title: this.translate.instant('PAGE.SITE.ACTION.UPDATE_CTRL'),
                          data: {
                                  updateInfo: updateInfo,
                                  updateFlag: true,
                                  siteName: siteName
                                }
                        };
          toastrTitle = this.translate.instant('PAGE.SITE.MESSAGE.TITLE.UPDATE_CTRL');
          successMessage = this.translate.instant('PAGE.SITE.MESSAGE.SUCCESS.UPDATE_CTRL');
          break;
    }

    const modalRef = this.appModalService.open(ControllerFormModalComponent, modalConfig);

    modalRef.afterClose$.subscribe(
      res => {
        if (!res.data) return;
        this.toastrOperatorService.showSuccessToastr(successMessage, toastrTitle);
        this.getSitesTableData();
      },
      error => {
        const errorMessage = error.error?.error?.message || error.message || error;
        this.toastrOperatorService.showErrorToastr(errorMessage, toastrTitle);
      }
    )
  }

  deleteSite(siteName: string): void {
    const model: BasicModal = {
      title: this.translate.instant('PAGE.SITE.ACTION.DELETE_SITE'),
      content: this.translate.instant('PAGE.SITE.ACTION.DELETE_SITE.CONTENT'),
      showCancelBtn: true,
      showConfirmBtn: true
    }
    this.basicModalService
      .openBasicDialog(model)
      .afterClosed()
      .subscribe(confirm => {
        if (confirm) {
          this.siteApiUrlService.deleteSite(siteName)
          .pipe(
            catchError((error) => {
              const errorMessage = error.error?.error?.message || error.message || error;
              this.toastrOperatorService.showErrorToastr(errorMessage, this.translate.instant('PAGE.SITE.MESSAGE.TITLE.DELETE_SITE'));
              return of([]);
            })
          )
          .subscribe((noneRes) => {
            if (!noneRes) {
              this.getSitesTableData();
              this.toastrOperatorService.showSuccessToastr(
                this.translate.instant('PAGE.SITE.MESSAGE.SUCCESS.DELETE_SITE', {'siteName': siteName}),
                this.translate.instant('PAGE.SITE.MESSAGE.TITLE.DELETE_SITE')
              );
            }
          })
        }
      });
  }

  deleteController(siteName: string, controllerName: string): void {
    const model: BasicModal = {
      title: this.translate.instant('PAGE.SITE.ACTION.DELETE_CTRL'),
      content: this.translate.instant('PAGE.SITE.ACTION.DELETE_CTRL.CONTENT'),
      showCancelBtn: true,
      showConfirmBtn: true
    }
    this.basicModalService
      .openBasicDialog(model)
      .afterClosed()
      .subscribe(confirm => {
        if (confirm) {
          this.siteApiUrlService.deleteController(siteName, controllerName)
          .pipe(
            catchError((error) => {
              const errorMessage = error.error?.error?.message || error.message || error;
              this.toastrOperatorService.showErrorToastr(errorMessage, this.translate.instant('PAGE.SITE.MESSAGE.TITLE.DELETE_CTRL'));
              return of([]);
            })
          )
          .subscribe((noneRes) => {
            if (!noneRes) {
              this.getSitesTableData();
              this.toastrOperatorService.showSuccessToastr(
                this.translate.instant('PAGE.SITE.MESSAGE.SUCCESS.DELETE_CTRL', {'controllerName': controllerName}),
                this.translate.instant('PAGE.SITE.MESSAGE.TITLE.DELETE_CTRL')
              );
            }
          })
        }
      });
  }

  getControllerErrorLog(siteName: string, controllerName: string, errorLog: string[], controllerErrorLogTemplateRef: TemplateRef<any>): void {
    this.openControllerErrorLogModal(siteName, controllerName, errorLog, controllerErrorLogTemplateRef);
  }
  
  openControllerErrorLogModal(siteName: string, controllerName: string, errorLog: string[], controllerErrorLogTemplateRef: TemplateRef<any>): void {
    const model: BasicModal = {
      title: this.translate.instant('PAGE.SITE.CTRL_ERROR_LOG', {'siteName': siteName, 'controllerName': controllerName}),
      template: controllerErrorLogTemplateRef,
      data: errorLog,
      showConfirmBtn: true,
      size: 'eighty-percent'
    }
    this.basicModalService
      .openBasicDialog(model)
      .afterClosed()
      .subscribe(confirm => { });
  }

}
