import { Component, OnInit, Inject, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { Observable } from "rxjs";
import { RegexService } from '../../../public-share/service/regex.service';
import { ApplicationModalConfig } from '../../../models/modal';
import { MODAL_DATA, PARENT_FORM, CHILD_FORM } from '../../../public-share/component/modal/modal.token';
import { BaseModal } from "../../../public-share/component/modal/modal.base";
import { SiteApiUrlService } from "../service/siteApiUrl.service";
import { ControllerPostSchema, ControllerPutSchema } from '../../../models/site';

@Component({
  selector: 'app-controller-form-modal',
  templateUrl: './controllerFormModal.component.html',
  styleUrls: ['../sites.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ControllerFormModalComponent implements OnInit, BaseModal {

  controllerForm = this.formBuilder.group({
    siteName: ['', [Validators.required]],
    controllerName: ['', [Validators.required, Validators.pattern(this.regexService.getRule('name_en_15'))]],
    ipAddress: ['', [Validators.required, Validators.pattern(this.regexService.getRule('ipv4'))]],
    loginAccount: ['', [Validators.required]],
    loginPassword: ['', [Validators.required]],
    description: ['', []],
  });

  updateFlag: boolean = false;
  updateInfo: ControllerPutSchema;
  updateSiteName: string;
  updateControllerName: string;
  originalIpAddress: string;
  originalCtrlUser: string;

  constructor(
    @Inject(MODAL_DATA) public modalConfig: ApplicationModalConfig,
    private formBuilder: FormBuilder,
    private siteApiUrlService: SiteApiUrlService,
    private regexService: RegexService
  ) { }

  ngOnInit(): void {
    this.updateSiteName = this.modalConfig.data.siteName;
    this.controllerForm.get('siteName').setValue(this.updateSiteName);
    this.controllerForm.get('siteName').disable();

    if (this.modalConfig.data && this.modalConfig.data.updateFlag) {
      this.updateFlag = true;
      this.updateInfo = this.modalConfig.data.updateInfo;
      this.updateControllerName = this.updateInfo.controllerName;
      this.controllerForm.get('controllerName').setValue(this.updateControllerName);
      this.controllerForm.get('controllerName').setValidators([Validators.pattern(this.regexService.getRule('name_en_15'))]);
      this.originalIpAddress = this.updateInfo.ipAddress
      this.controllerForm.get('ipAddress').setValue(this.updateInfo.ipAddress);
      this.controllerForm.get('ipAddress').setValidators([Validators.pattern(this.regexService.getRule('ipv4'))]);
      // reset validators of password when account user changed >>
      this.originalCtrlUser = this.updateInfo.loginAccount;
      this.controllerForm.get('loginAccount').setValidators([]);
      this.controllerForm.get('loginAccount').valueChanges.subscribe(value => {
        if (value == this.originalCtrlUser) {
          this.controllerForm.get('loginPassword').setValidators([]);
        } else {
          this.controllerForm.get('loginPassword').setValidators([Validators.required]);
        }
        this.controllerForm.get('loginPassword').setValue('');
        this.controllerForm.markAllAsTouched();
        this.controllerForm.updateValueAndValidity();
      });
      this.controllerForm.get('loginAccount').setValue(this.updateInfo.loginAccount);
      // << reset validators of password when account user changed
      this.controllerForm.get('description').setValue(this.updateInfo.description);
    }
    this.modalConfig[PARENT_FORM].addControl(CHILD_FORM, this.controllerForm);
  }

  submit(): Observable<any> {
    const data = this.controllerForm.getRawValue();

    return new Observable<any>(subscriber => {
      if (this.updateFlag == true) {
        const putControllerParams: ControllerPutSchema = {
          controllerName: data.controllerName,
          ipAddress: data.ipAddress,
          loginAccount: data.loginAccount,
          description: data.description
        };
        if (data.controllerName == '' || data.controllerName == this.updateControllerName) { delete putControllerParams.controllerName; }
        if (data.ipAddress == '' || data.ipAddress == this.originalIpAddress) { delete putControllerParams.ipAddress; }
        if (data.loginAccount == '') { delete putControllerParams.loginAccount; }
        if (data.loginPassword != '') {
          putControllerParams.loginPassword = data.loginPassword;
        }
        this.siteApiUrlService.putController(this.updateSiteName, this.updateControllerName, putControllerParams)
        .subscribe(
          data =>
            subscriber.next(true),
          err =>
            subscriber.error(err)
        );
      } else {
        const postControllerParams: ControllerPostSchema = {
          controllerName: data.controllerName,
          ipAddress: data.ipAddress,
          loginAccount: data.loginAccount,
          loginPassword: data.loginPassword,
          description: data.description
        };
        this.siteApiUrlService.postController(this.updateSiteName, postControllerParams)
        .subscribe(
          data =>
            subscriber.next(true),
          err =>
            subscriber.error(err)
        );
      }
    });
  }

}
