import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { apiUrl } from '../../../public-share/service/environment';
import {
  SitePostSchema,
  SitePutSchema,
  SiteRes,
  ControllerPostSchema,
  ControllerPutSchema,
  SiteErrorLogRes,
  ControllerErrorLogRes
} from '../../../models/site';

@Injectable({
  providedIn: 'root'
})
export class SiteApiUrlService {

  constructor(
    private http: HttpClient
  ){ }
  public postSite(site: SitePostSchema) {
    return this.http.post(`${apiUrl}/sites`, site);
  }

  public getSites(): Observable<SiteRes[]> {
    return this.http.get<SiteRes[]>(`${apiUrl}/sites`);
  }

  public putSite(siteName: string, site: SitePutSchema) {
    return this.http.put(`${apiUrl}/sites/${siteName}`, site);
  }

  public deleteSite(siteName: string) {
    return this.http.delete(`${apiUrl}/sites/${siteName}`);
  }

  public postController(siteName: string, controller: ControllerPostSchema) {
    return this.http.post(`${apiUrl}/sites/${siteName}/controllers`, controller);
  }

  public putController(siteName: string, controllerName: string, controller: ControllerPutSchema) {
    return this.http.put(`${apiUrl}/sites/${siteName}/controllers/${controllerName}`, controller);
  }

  public deleteController(siteName: string, controllerName: string) {
    return this.http.delete(`${apiUrl}/sites/${siteName}/controllers/${controllerName}`);
  }

  public getSitesErrorLog(hour: number, count: number): Observable<SiteErrorLogRes[]> {
    return this.http.get<SiteErrorLogRes[]>(`${apiUrl}/sites/errorLog?hour=${hour}&count=${count}`);
  }

  public getControllerErrorLog(siteName: string, controllerName: string, controller: ControllerPutSchema, hour: number, count: number) {
    return this.http.get(`${apiUrl}/sites/${siteName}/controllers/${controllerName}/errorLog?hour=${hour}&count=${count}`);
  }

}