import { Injectable, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Subject, BehaviorSubject, Observable, of } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import {
  UserRes,
  UserPutSchema,
  AccountMgmtTableData
} from '../../../models/account-management';
import { BasicModal, ApplicationModalConfig } from '../../../models/modal';
import { AccountMgmtApiUrlService } from './account-management-api-url.service';
import { ToastrOperatorService } from '../../../public-share/service/toastr-operator.service';
import { BasicModalService, ApplicationModalService } from '../../../public-share/component/modal/modal.service';
import { AccountMgmtFormModalComponent } from '../formModal/account-management-modal.component';

@Injectable({
  providedIn: 'root'
})
export class AccountMgmtService implements OnInit, OnDestroy {

  constructor(
    private router: Router,
    private toastrOperatorService: ToastrOperatorService,
    private accountMgmtApiUrlService: AccountMgmtApiUrlService,
    private appModalService: ApplicationModalService,
    private basicModalService: BasicModalService,
    private translate: TranslateService
  ) { }

  private _accountMgmtTableData = new BehaviorSubject<AccountMgmtTableData[]>([]);
  private _loading = new BehaviorSubject<boolean>(true);
  // private _message = new Subject<MessageModel>();
  readonly accountMgmtTableData$: Observable<any> = this._accountMgmtTableData.asObservable();
  readonly loading$: Observable<boolean> = this._loading.asObservable();
  // readonly message$: Observable<MessageModel> = this._message.asObservable();

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this._accountMgmtTableData.unsubscribe();
    this._loading.unsubscribe();
    // this._message.unsubscribe();
  }

  getAccountMgmtTableData(): void {
    this._loading.next(true);
    this.accountMgmtApiUrlService.getUsers()
      .pipe(
        catchError((error) => {
          const errorMessage = error.error?.error?.message || error.message || error;
          this.toastrOperatorService.showErrorToastr(errorMessage, this.translate.instant('PAGE.ACCOUNT_MGMT.MESSAGE.TITLE.GET_SITE_TABLE_DATA'));
          return of([]);
        })
      )
      .subscribe((usersRes: UserRes[]) => {
        const accountMgmtTableData: AccountMgmtTableData[] = usersRes.sort(
          (front, behind) => {
            const frontUserRole = front.role.toUpperCase();
            const behindUserRole = behind.role.toUpperCase();
            if (frontUserRole < behindUserRole) { return -1; }
            return 0;
          }
        );

        this._loading.next(false);
        this._accountMgmtTableData.next(JSON.parse(JSON.stringify(accountMgmtTableData)));
    });
  }

  addUser(): void {
    this.openAccountMgmtFormModal('add');
  }

  updateUserAccount(username: string, rowData: AccountMgmtTableData): void {
    this.openAccountMgmtFormModal('update', username, rowData);
  }

  openAccountMgmtFormModal(action: ('add'|'update'), username?: string, updateInfo?: AccountMgmtTableData): void {
    let modalConfig: ApplicationModalConfig;
    let toastrTitle: string = '';
    let successMessage: string = '';
    
    switch(action) {
      case 'add':
          modalConfig = {
                          title: this.translate.instant('PAGE.ACCOUNT_MGMT.ACTION.ADD_USER')
                        };
          toastrTitle = this.translate.instant('PAGE.ACCOUNT_MGMT.MESSAGE.TITLE.ADD_USER');
          successMessage = this.translate.instant('PAGE.ACCOUNT_MGMT.MESSAGE.SUCCESS.ADD_USER');
          break;
      case 'update':
          modalConfig = {
                          title: this.translate.instant('PAGE.ACCOUNT_MGMT.ACTION.UPDATE_USER'),
                          data: {
                                  updateInfo: updateInfo,
                                  username: username,
                                  updateFlag: true
                                }
                        };
          toastrTitle = this.translate.instant('PAGE.ACCOUNT_MGMT.MESSAGE.TITLE.UPDATE_USER');
          successMessage = this.translate.instant('PAGE.ACCOUNT_MGMT.MESSAGE.SUCCESS.UPDATE_USER');
          break;
    }

    const modalRef = this.appModalService.open(AccountMgmtFormModalComponent, modalConfig);

    modalRef.afterClose$.subscribe(
      res => {
        if (!res.data) return;
        this.toastrOperatorService.showSuccessToastr(successMessage, toastrTitle);
        setTimeout( () => { this.getAccountMgmtTableData(); }, 500);
      },
      error => {
        const errorMessage = error.error?.error?.message || error.message || error;
        this.toastrOperatorService.showErrorToastr(errorMessage, toastrTitle);
      }
    )
  }

  deleteUserAccount(userName: string): void {
    const model: BasicModal = {
      title: this.translate.instant('PAGE.ACCOUNT_MGMT.ACTION.DELETE_USER'),
      content: this.translate.instant('PAGE.ACCOUNT_MGMT.ACTION.DELETE_USER.CONTENT', {'username': userName}),
      showCancelBtn: true,
      showConfirmBtn: true
    }
    this.basicModalService
      .openBasicDialog(model)
      .afterClosed()
      .subscribe(confirm => {
        if (confirm) {
          this.accountMgmtApiUrlService.deleteUser(userName)
          .pipe(
            catchError((error) => {
              const errorMessage = error.error?.error?.message || error.message || error;
              this.toastrOperatorService.showErrorToastr(errorMessage, this.translate.instant('PAGE.ACCOUNT_MGMT.MESSAGE.TITLE.DELETE_USER'));
              return of([]);
            })
          )
          .subscribe((noneRes) => {
            if (!noneRes) {
              this.getAccountMgmtTableData();
              this.toastrOperatorService.showSuccessToastr(
                this.translate.instant('PAGE.ACCOUNT_MGMT.MESSAGE.SUCCESS.DELETE_USER', {'username': userName}),
                this.translate.instant('PAGE.ACCOUNT_MGMT.MESSAGE.TITLE.DELETE_USER')
              );
            }
          })
        }
      });
  }

}
